@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  background-color: pink;
}

#root, body, html {
  width: 100%;
  -webkit-overflow-scrolling: touch;
  margin: 0;
  padding: 0;
  min-height: 100%;
}

html {
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  display: block;
}

.App{
  flex:1 1;
  display:flex;
  flex-Direction: column;
  justify-Content:center;
  text-align: center;
  align-items: center;
  height:100%;
  width: 100%;
  margin: 0;
}
.App-header {
  background-color: pink;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color:black;
}

.App-header h1{
  margin-top: 10vh;
  max-width: 95vw;
  font-size: calc(40px + 2vmin);
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
}

.App-header h2{
  margin-top: 10vh;
  margin-bottom: 10vh;
  max-width: 95vw;
  font-size: calc(5px + 2vmin);
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
}

.social-container {
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  margin-bottom: 5rem;
}

.social-container h3{
  font-family: 'Ubuntu', sans-serif;
  font-weight: 500;
  font-size: calc(15px + 2vmin);
}

a.social {
  margin: 0 calc(1rem + 2vmin);
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}

a.whatsapp {
  color: green;
  font-size: calc(25px + 2vmin);
}

a.facebook {
  color: #4968ad;
  font-size: calc(25px + 2vmin);
}

a.twitter {
  color: #49a1eb;
  font-size: calc(25px + 2vmin);
}

a.instagram {
  color:purple;
  font-size: calc(25px + 2vmin);
}


.orders-container {
    background-color: pink;
  }
  
  .orders-container h3 {
    margin: 0;
  }
  
  .orders-steps-container {
    display: flex;
    justify-content: center;
    background-color: #FFF;
    margin-bottom: 30px;
  }
  
  .orders-steps-content {
    display: flex;
    padding: 25px 0;
    width: 70%;
  }
  
  .steps-title {
    font-weight: bold;
    font-size: 36px;
    line-height: 34px;
    letter-spacing: -0.015em;
    color: var(--primary-color);
    margin-right: 100px;
  }
  
  .steps-items {
    list-style-type: none;
  }
  
  .steps-items li {
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: -0.015em;
    color: var(--secondary-color);
  }
  
  .steps-items .steps-number {
    font-size: 24px;
    font-style: normal;
    font-weight: bold;
    line-height: 33px;
    letter-spacing: -0.015em;
    color: var(--primary-color);
    margin-right: 10px;
  }
  
  .orders-list-container {
    display: flex;
    justify-content: center;
  }
  
  .orders-list-items {
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fill, 150px);
    grid-gap: 35px 10px;
    justify-content: center;
  }
  
  .order-card-container {
    background-color: #FFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    height: 20vh;
    padding: 0px;
    cursor: pointer;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
  }
  
  .order-card-container.selected {
    border: 4px solid #008E5B;
    box-sizing: border-box;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
  
  .order-card-container:hover {
    transform: scale(1.01);
  }
  
  .order-card-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: -0.015em;
    color: var(--primary-color);
    text-align: center;
  }
  
  .order-card-container.selected .order-card-title {
    color: #008E5B;
  }
  
  .order-card-image {
    border-radius: 10px;
    width: 100%;
    height: 100%;
  }
  
  .order-card-price {
    font-weight: bold;
    font-size: 24px;
    line-height: 33px;
    letter-spacing: -0.015em;
    color: var(--primary-color);
    text-align: left;
  }
  
  .order-card-container.selected .order-card-price {
    color: #008E5B;
  }
  
  .order-card-description {
    margin-top: 15px;
    padding-top: 8px;
  }
  
  .order-card-description h3 {
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.015em;
    color: var(--secondary-color);
  }
  
  .order-card-description p {
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: -0.015em;
  
    color: var(--secondary-color);
  }
  
  .order-location-container {
    display: flex;
    justify-content: center;
  }
  
  .order-location-content {
    width: 70%;
    margin: 30px 0 120px 0;
    padding: 20px 0;
    height: 400px;
    background: #FFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
  
  .filter-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .filter {
    width: 80%;
  }
  
  .filter > div {
    border-radius: 10px;
    min-height: 60px;
  }
  
  .leaflet-container {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    margin-top: 20px;
    z-index: 0;
  }
  
  .order-location-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.015em;
    color: var(--secondary-color);
  }
  
  .order-summary-container {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
  }
  
  .order-summary-content {
    width: calc(70% - 40px);
    display: flex;
    justify-content: space-between;
    background: #FFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    background-color: var(--primary-color);
    color: #FFF;
    padding: 20px;
  }
  
  .amount-selected-container {
    font-weight: normal;
    font-size: 11px;
    line-height: 15px;
    letter-spacing: -0.015em;
    display: block;
    margin-bottom: 10px;
  }
  
  .amount-selected {
    font-weight: bold;
    font-size: 16px;
    line-height: 15px;
    letter-spacing: -0.015em;
    margin-right: 5px;
  }
  
  .order-summary-total {
    font-weight: normal;
    font-size: 14px;
    line-height: 15px;
    letter-spacing: -0.015em;
    color: #FFF;
    display: block;
  }
  
  .order-summary-price {
    font-weight: bold;
    font-size: 18px;
    line-height: 19px;
    letter-spacing: -0.015em;
    color: #FFF;
    margin-right: 5px;
  }
  
  .order-summary-make-order {
    width: 220px;
    height: 45px;
    background: #FFF;
    border-radius: 10px;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.015em;
    color: var(--primary-color);
    border: 0;
    cursor: pointer;
  }
  
  .order-summary-make-order:focus {
    outline: none;
  }
  
  .order-summary-make-order:hover {
    background-color: #fcf2f2;
    transform: scale(1.01);
  }
  
  @media only screen and (max-width: 768px) {
    .orders-steps-content {
      display: flex;
      flex-direction: column;
      padding: 0;
      width: 90%;
    }
  
    .steps-items {
      padding: 0
    }
  
    .order-location-content {
      width: 90%;
    }
  
    .order-location-container {
      margin-bottom: 20px;
    }
  
    .order-summary-content {
      width: 80%;
      display: flex;
      flex-direction: column;
    }
  
    .order-summary-make-order {
      margin-top: 15px;
      width: 100%;
    }
  }
